/*----------------- header -----------------*/

#header {
  position: fixed;
  width: 100%;
  border-bottom: 1px solid rgba(107, 114, 128, .3);
  background-color: #fff;
  z-index: 99;
}

.header-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0 1.3rem;
  height: 4rem;
}

.header-logo {
  margin-right: 4rem;
}

.m-header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  padding: 0 1.3rem;
  position: relative;
}

.m-header-logo {
  margin-left: 2.5rem;
}

.m-header-acc {
  display: flex;
}

/*----------------- header -----------------*/


/*----------------- main -----------------*/

.swiper-bg {
  background: url(../images/bg01.jpg) top center no-repeat;
  background-size: cover;
}

.main-grid {
  display: grid;
  column-gap: 2.5rem;
  row-gap: 1.5rem;
  max-width: 80rem;
  grid-template-columns: repeat(3,minmax(0,1fr));
  justify-items: center;
}

.grid-hover {
  transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
}

.grid-hover:hover {
  transform: scale(1.1);
  -webkit-transform:scale(1.1);
  -moz-transform:scale(1.1);
  -o-transform:scale(1.1);
}

/*----------------- main -----------------*/



